            <li>prepend</li>
            <li>after</li>
            <li>before</li>
            <li>remove</li>
        </ol>
        <ul class="content">
            <li>
                <h3 class="title">append</h3>
                <div class="content">
                    append(str, move)strhtmlGj("#test").append("&lt;div&gt;&lt;/div&gt;")testdiv</br>
