                    movetrue</br>
                    Gj("#test").after("&lt;div&gt;&lt;/div&gt;")testdiv</br>
                    Gj("#test").after(div)testdiv
                </div>
            </li>
            <li>
                <h3 class="title">before</h3>
                <div class="content">
                    before(str, move)strhtml</br>
                    movetrue</br>
                    Gj("#test").before("&lt;div&gt;&lt;/div&gt;")testdiv</br>
                    Gj("#test").before(div)testdiv
                </div>
            </li>
            <li>
                <h3 class="title">remove</h3>
                <div class="content">
                    remove()Gj("#test").remove()test
                </div>
            </li>

        </ul>
    </div>
</template>

<script>
export default {};
</script>

