                    height()gjTool('#test').height();test
                </div>
            </li>
            <li>
                <h3 class="title">offset</h3>
                <div class="content">
                    offset()offsetTop,offsetLeftgjTool('#test').offset().top;testtop
                </div>
            </li>
            <li>
                <h3 class="title">scrollTop</h3>
                <div class="content">
                    scrollTop(value)scrollTopgjTool('#test').scrollTop();testscrollTop
                </div>
            </li>
            <li>
                <h3 class="title">scrollLeft</h3>
                <div class="content">
                    scrollLeft(value)scrollTopgjTool('#test').scrollLeft();testscrollLeft
                </div>
            </li>
        </ul>
    </div>
</template>

