<template>
    <div class="type-con">
        <ol class="sub-nav clearfix">
            <li>queue</li>
            <li>delay</li>
            <li>dequeue</li>
        </ol>
        <ul class="content">
            <li>
                <h3 class="title">queue</h3>
                <div class="content">
                    queue(obj, fn);
                    objdocument</br>
                    fn</br>
                </div>
            </li>
            <li>
                <h3 class="title">delay</h3>
                <div class="content">
                    delay(obj, time);
                    objdocument</br>
                    time</br>
                </div>
            </li>
            <li>
                <h3 class="title">dequeue</h3>
                <div class="content">
                    delay(obj,fn);
                    objdocument</br>
                    fn</br>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {};
</script>

<style>

</style>



