<template>
    <div class="type-con">
        <ol class="sub-nav clearfix">
            <li></li>
            <li>drag</li>
        </ol>
        <ul class="content">
            <li>
                <h3 class="title"></h3>
                <div class="content">
                    extendGj.fn.protoTypeGj.protoType</br>
                    </br>
                    Gj.protoType.alert = function(a){</br>
                        alert(a);</br>
                    }</br>
                    Gj.fn.protoType.alert = function(){</br>
                        alert(Gj(this).val());</br>
                    }</br>
                    Gj.extend({</br>
                        alert: function(){</br>
                            alert(a);</br>
                        }
                    })</br>
                    Gj.fn.extend({</br>
                        alert : function(){</br>
                        alert(Gj(this).val());</br>
                    }
                </div>
            </li>
            <li>
                <h3 class="title">drag</h3>
                <div class="content">
                    drag()nodrag(true)nodrag()nodrag(false)</br>
                    </br>
                    Gj("#div").drag();//</br>
                    Gj("#div").nodrag();//</br>
                    Gj("#div").nodrag(false);//</br>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {};
</script>

<style>

</style>



