<template>
    <div class="type-con">
        <ol class="sub-nav clearfix">
            <li>attr</li>
            <li>removeAttr</li>
            <li>prop</li>
            <li>data</li>
            <li>val</li>
            <li>html</li>
            <li>text</li>
            <li>empty</li>
        </ol>
        <ul class="content">
            <li>
                <h3 class="title">attr</h3>
                <div class="content">
                    attr(name,value)gjTool('img').attr("src","www.baidu.com");imgsrcwww.baidu.com</br>
                    attr({name:value})gjTool('img').attr({"src":"www.baidu.com"});imgsrcwww.baidu.com</br>
                    attr(name)gjTool('img').attr("src");imgsrc
                </div>
            </li>
            <li>
                <h3 class="title">removeAttr</h3>
                <div class="content">
                    removeAttr(name)gjTool('img').removeAttr("src");imgsrc
                </div>
            </li>
            <li>
                <h3 class="title">prop</h3>
                <div class="content">
                    prop(name,value)gjTool('input').prop("checked","checked");inputchecked</br>
                    prop(name)gjTool('input').prop("checked");inputchecked
                </div>
            </li>
            <li>
                <h3 class="title">data</h3>
                <div class="content">
                    data(name,value)datagjTool('div').data("id","123");divdata-id123</br>
                    data(name)datagjTool('div').data("id");divdata-id
                </div>
            </li>
            <li>
                <h3 class="title">val</h3>
                <div class="content">
                    val(value)valuegjTool('input').val("123");inputvalue123</br>
                    val()valuegjTool('input').val();inputvalue
                </div>
            </li>
            <li>
                <h3 class="title">html</h3>
                <div class="content">
                    html(value)innerHTMLgjTool('#div').html("123");divinnerHTML123</br>
                    html()innerHTMLgjTool('#div').html();divinnerHTML
                </div>
            </li>
            <li>
                <h3 class="title">text</h3>
                <div class="content">
                    text(value)innerTextgjTool('#div').text("123");divinnerText123</br>
                    text()innerTextgjTool('#div').text();divinnerText
                </div>
            </li>
            <li>
                <h3 class="title">empty</h3>
                <div class="content">
                    empty(),html("")innerHTML=""
                    gjTool('#div').empty();div
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {};
</script>

<style>

</style>



